.Todo_Item{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    background-color: aquamarine;
    width: 90%;
    height: 50px; 
    margin: 0 auto 10px;    
    font-weight: bold;
    max-width: 400px;
    border-radius: 5px;
}


.Todo_Item--Text{
    font-size: 1.9rem;
}


.Text_Complete{

    text-decoration:line-through

}

.Todo_Item--Close{
    position: absolute;
    top:-5px;
    right: -5px;
    font-size: 2.5rem;
}

